<template>
    <div class="redact">
        <!-- 头部开始 -->
        <header style="line-height: 0.5rem">
          <Head>
            <div slot='a' class="he login_he">
              <div class="left">
                <a-Icon type="left" @click="$router.push('/Account')" style="line-height: 0.5rem" />
              </div>
              <div class="content" style="line-height: 0.5rem">编辑地址</div>
              <div class="right" v-if='show==false' @click='xshow()'>编辑</div>
              <div class="right" v-else @click='hidden()'>完成</div>
            </div>
          </Head>
        </header>
        <!-- 头部结束 -->

        <!-- 地址开始 -->
        <div style="margin-top: 0.2rem;border-bottom:1px solid gainsboro;border-top:1px solid gainsboro;">
        <div class="newly" v-for='(v,i) in list' :key="i">
          <ul>
            <li>{{v.address}}</li>
            <li>{{v.phone}}</li>
          </ul>
          <div v-show='show' @click='del(v)'>x</div>
        </div>
        </div>
        <!-- 地址结束 -->

        <!-- 新增地址开始 -->
        <router-link class="photo" tag="div" to="/Newly">
          <div class="photo_left">新增地址</div>
          <div class="photo_right">
            <a-Icon type="right" />
          </div>
        </router-link>
        <!-- 新增地址结束 -->
    </div>
</template>

<script>
import Head from '@/page/header/Head' 
import axios from 'axios'  
export default{
    name:'Redact',
    components:{Head},
    data() {
      return {
        list:[],
        show:false
      }
    },
    created() {
      axios.get('http://elm.cangdu.org/v1/users/38715/addresses').then((res)=>{
          // console.log(res)
          this.list = res.data
      })
    },
    methods: {

      // 删除显示
      xshow(){
        this.show = true
      },
      // 删除隐藏
      hidden(){
        this.show = false
      },
      // 删除送餐地址
      del(v){
        var path = 'http://elm.cangdu.org/v1/users/38715/addresses/'+v.id
        axios.delete(path).then((res)=>{
            // console.log(res)
            axios.get('http://elm.cangdu.org/v1/users/38715/addresses').then((res)=>{
                // console.log(res)
                this.list = res.data
            })
        })
      }
    },
}
</script>

<style lang="scss">
.redact{
    width: 100%;
    height: 100%;
    background: #F5F5F5;
    .photo,.newly{
        width: 100%;
        background:white;
        font-size:0.3rem;
        padding:0rem 0.2rem;
    }
    .photo{    
        height:0.7rem;
        line-height:0.6rem;
        margin-top: 0.2rem;
        border-bottom:1px solid gainsboro;
        border-top:1px solid gainsboro;
    .photo_left{
        float: left;
    }
    .photo_right{
      color: gray;
      float: right;
    }
  }
  .newly{
      height: 1rem;
      margin-top: 0.02rem;
      padding-top: 0.1rem;
      display: flex;
      ul{
        width: 95%;
        height: 100%;
      li{
        font-size:0.24rem;
        color: black;
      }}
  }
}
</style>






